import React, { useEffect, useRef, useState } from 'react';
import WaveSurfer from 'wavesurfer.js';
import RegionsPlugin from "wavesurfer.js/dist/plugins/regions.js"
import TimelinePlugin from "wavesurfer.js/dist/plugins/timeline.js"


export interface Point {
    x: number;
    y: number;
}

export interface Graph {
    name: string;
    points: Point[];
    color: string;
}

export interface Region {
    start: number;
    end: number;
    content: string;
    color?: string;
    drag?: boolean;
    resize?: boolean;
}

export class Region {
    constructor(public start: number, public end: number, public content: string, public color?: string, public drag?: boolean, public resize?: boolean) {
    }
}

export class Graph {
    constructor(public name: string, public points: Point[], public color: string) {
    }
}


export interface WavesurferViewerProps {
    audioSrc: string;
    regions: Region[];
    graphs?: Graph[];
    onReady: () => void;
}

export const WavesurferViewer: React.FC<WavesurferViewerProps> = ({ audioSrc, regions, graphs, onReady }) => {
    const waveformRef = useRef<HTMLDivElement>(null);
    const [waveform, setWaveform] = useState<WaveSurfer | null>(null);
    const [wsRegions, setWsRegions] = useState<RegionsPlugin | null>(null);
    const [activeRegion, setActiveRegion] = useState<any>(null);
    const [zoomMinPxPerS, setZoomMinPxPerS] = useState(100);
    const [isPlaying, setIsPlaying] = useState(false);
    const [loopRegion, setLoopRegion] = useState(false);

    const getWsOptions = () => ({
        container: waveformRef.current!,
        waveColor: 'violet',
        progressColor: 'purple',
        responsive: true,
        xhr: { cache: 'default', mode: 'no-cors' },
        autoScroll: true,
    });

    const updateRegions = (regions: Region[]) => {
        if (!wsRegions) return;

        wsRegions.clearRegions();
        regions.forEach(region => {
            wsRegions.addRegion({
                start: region.start,
                end: region.end,
                content: region.content,
                color: region.color || `rgba(${Math.random() * 255}, ${Math.random() * 255}, ${Math.random() * 255}, 0.5)`,
                drag: region.drag,
                resize: region.resize,
            });
        });
    };



    useEffect(() => {
        if (!waveformRef.current) return;

        const ws = WaveSurfer.create(getWsOptions());
        const regionsPlugin = ws.registerPlugin(RegionsPlugin.create());
        const timelinePlugin = ws.registerPlugin(TimelinePlugin.create({
            height: 10,
            timeInterval: 0.1,
            primaryLabelInterval: 1,
            style: {
                fontSize: '10px',
                color: '#6A3274',
            },
        }));

        setWaveform(ws);
        setWsRegions(regionsPlugin);

        ws.load(audioSrc);
        onReady();

        // ws.renderer.on("scroll", updateChart);

        ws.on("ready", () => {
            ws.zoom(zoomMinPxPerS);
            setTimeout(() => {
                updateRegions(regions);
            }, 500);
        });



        regionsPlugin.on('region-out', (region) => {
            if (activeRegion === region) {
                if (loopRegion) {
                    region.play();
                } else {
                    setActiveRegion(null);
                }
            }
        });

        regionsPlugin.on('region-clicked', (region, e) => {
            setIsPlaying(true);
            setLoopRegion(true);
            setActiveRegion(region);
            region.play();
        });

        ws.on('interaction', () => {
            setActiveRegion(null);
        });

        return () => {
            ws.destroy();
        };
    }, [audioSrc]);



    useEffect(() => {
        updateRegions(regions);
    }, [regions]);

    return (
        <div>
            <div ref={waveformRef} style={{ width: "100%" }} />
            <div style={{ textAlign: 'center', width: "50%" }}>
                Zoom
                <input
                    type="range"
                    min={1}
                    max={350}
                    value={zoomMinPxPerS}
                    onChange={(e) => {
                        const value = Number(e.target.value);
                        setZoomMinPxPerS(value);
                        waveform?.zoom(value);
                    }}
                />
            </div>
            <button
                onClick={() => {
                    if (isPlaying) {
                        waveform?.pause();
                        setIsPlaying(false);
                        setLoopRegion(false);
                    } else {
                        waveform?.play();
                        setIsPlaying(true);
                    }
                }}
            >
                {!isPlaying ? '▶️' : '⏸️'}
            </button>


        </div>
    );
};

